
.btn-light{
    color: $accent-color;
    background-color: white;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.31;
    height: 44px;
    border-radius: 22px;

    cursor: pointer;

    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover, &:focus{
      background-color: #ecebeb;
    }  
}

.btn-dark{
    color: #fff;
    background-color: $accent-color;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.32;
    height: 44px;
    border-radius: 22px;

    cursor: pointer;

    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover, &:focus{
      background-color: #C7133E;
    }  
}




