.backdrop {
  position: fixed;
  top: 0;
  right: 0;
  width: 250px;
  height: 502px;
  background-color:transparent;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  overflow-y: scroll;
  z-index: 100;
  @media screen and (min-width: 768px) and (max-width: 1279px) {
      width: 254px;
      height: 1020px;
  }
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.backdrop {
  .nav-list {
    display: flex;
    flex-direction: column;
    margin-top: 19px;
    margin-left: 21px;

    @media screen and (min-width: 768px) and (max-width: 1279px) {
      margin-left: 24px;
    }
  }

  .nav-list-item {
    display: flex;
    align-items: center;
    height: 42px;
    border-top: 1px solid #e18298;

    &:last-child {
      border-bottom: 1px solid #e18298;
    }
  }

  .modal-menu-wrapper {
    position: absolute;
    right: 0;
    background-color: #e17992;
    width: 250px;
    height: 502px;
    padding-top: 60px;
    filter: drop-shadow(-8px 0px 20px rgba(0, 0, 0, 0.25));

    @media screen and (min-width: 768px) and (max-width: 1279px) {
      width: 254px;
      height: 1020px;
    }
  }

  @media screen and (max-width: 767px) {
    .btn-light.wrapper {
      display: block;
      width: 209px;
    }
  }
}

.btn-light.wrapper {
  display: flex;
  margin: 0 auto;
  align-items: center;
  justify-content: center;

  &::after {
    content: '';
    display: inline-block;
    background-image: url(../images/sprite.svg#icon-btn-arrow);
    background-size: contain;
    background-repeat: no-repeat;

    width: 5px;
    height: 8px;
    margin-left: 15px;
  }
}

.modal-close{
    position: absolute;
    width: 14px;
    height: 14px;
    top: 23px;
    right: 28px;
    background-color: transparent;
    outline: 1px solid red;

    @media screen and (min-width:768px) and (max-width:1199px){
      right: 34px;
    }
}
